import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import {SplashScreen} from '@ionic-native/splash-screen';
import {StatusBar} from '@ionic-native/status-bar';

import {MyApp} from './app.component';
import {HomePage} from '../pages/home/home';
import {ApiProvider} from '../providers/api/api';
import {HttpClientModule} from "@angular/common/http";
import {ProfilePageModule} from "../pages/profile/profile.module";
import {RequestPageModule} from "../pages/request/request.module";
import {PharmahomePageModule} from "../pages/pharmahome/pharmahome.module";
import {PharmacyPageModule} from "../pages/pharmacy/pharmacy.module";
import {RequestsPageModule} from "../pages/requests/requests.module";
import {AngularFireModule} from "@angular/fire";
import {AngularFireDatabaseModule} from "@angular/fire/database";
import {HistoryreqPageModule} from "../pages/historyreq/historyreq.module";
import {OrdonancePageModule} from "../pages/ordonance/ordonance.module";
import {LocalNotifications} from '@ionic-native/local-notifications';
import {Camera} from '@ionic-native/camera';
import {CurrentPageModule} from "../pages/current/current.module";
import {GeocityPageModule} from "../pages/geocity/geocity.module";
import {Geolocation} from '@ionic-native/geolocation';
import {Badge} from '@ionic-native/badge';

const environment = {
    production: false,
    firebase: {
        apiKey: "AIzaSyAdObHow109uB-4aPdz3qxklOTE6bjcBGs",
        authDomain: "dispomed-da61b.firebaseapp.com",
        databaseURL: "https://dispomed-da61b.firebaseio.com",
        projectId: "dispomed-da61b",
        storageBucket: "dispomed-da61b.appspot.com",
        messagingSenderId: "521388710211"
    }
};

@NgModule({
    declarations: [
        MyApp,
        HomePage
    ],
    imports: [
        BrowserModule,
        HttpClientModule,
        ProfilePageModule,
        RequestPageModule,
        PharmahomePageModule,
        PharmacyPageModule,
        RequestsPageModule,
        HistoryreqPageModule,
        OrdonancePageModule,
        CurrentPageModule,
        GeocityPageModule,
        AngularFireDatabaseModule,
        AngularFireModule.initializeApp(environment.firebase),
        IonicModule.forRoot(MyApp)
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        HomePage
    ],
    providers: [
        StatusBar,
        SplashScreen,
        LocalNotifications,
        Badge,
        Camera,
        Geolocation,
        Badge,
        {provide: ErrorHandler, useClass: IonicErrorHandler},
        ApiProvider
    ]
})
export class AppModule {
}



