import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {HomePage} from "../home/home";
import {ProfilePage} from "../profile/profile";
import {RequestPage} from "../request/request";
import {RequestsPage} from "../requests/requests";
import {Badge} from "@ionic-native/badge";
import {LocalNotifications} from "@ionic-native/local-notifications";

/**
 * Generated class for the CurrentPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-current',
    templateUrl: 'current.html',
})
export class CurrentPage {

    cr: any;

    constructor(public navCtrl: NavController, public navParams: NavParams, public api: ApiProvider) {

        if (!this.api.curReqkey) {
            //this.api.requestRestarter()
            this.navCtrl.setRoot(HomePage)
        } else {
            this.api.afdb.object('dispomed_requests/' + this.api.curReqkey).valueChanges().subscribe((data: any) => {
                this.cr = data;
                let na = this.api.countObj(data.answers);
                this.api.setbadge(na);
                this.api.notify(111,'Vous avez ' + na + ' réponses')
            });
        }
        this.api.requestRestarter()
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad CurrentPage');
    }

    cancelRequest() {
        this.api.closeRequest();
        this.api.curReqkey = null;
        localStorage.removeItem('dispomed_na');
        this.api.setbadge(0);
        this.navCtrl.setRoot(HomePage)
    }

    goProfile() {
        this.navCtrl.push(ProfilePage)
    }

    goRequest() {
        this.navCtrl.push(RequestPage, this.cr)
    }

    goRequests() {
        if (this.api.user.isofficinevalidated == false) {
            this.api.showAlert('Votre compte pharmacien n\'est pas encore validée.', '');
            return false
        } else {
            this.navCtrl.push(RequestsPage)
        }
    }

}



