import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {Geolocation} from '@ionic-native/geolocation';
import {ApiProvider} from "../../providers/api/api";
import {ProfilePage} from "../profile/profile";
import {HomePage} from "../home/home";

/**
 * Generated class for the GeocityPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-geocity',
    templateUrl: 'geocity.html',
})
export class GeocityPage {

    geo: any = {};
    items: any[];
    item: any;

    constructor(public navCtrl: NavController, public navParams: NavParams, private geolocation: Geolocation, public api: ApiProvider) {
        this.geo = {};
        this.api.newrequest.city = this.api.user.city ? this.api.user.city : null;

        this.geolocation.getCurrentPosition().then((resp) => {
          // resp.coords.latitude
          // resp.coords.longitude
            this.geo.timesamp = resp.timestamp;
            this.geo.latitude = resp.coords.latitude;
            this.geo.longitude = resp.coords.longitude;
        }).catch((error) => {
          console.log('Error getting location', error);
        });
        /*
        this.geolocation.watchPosition().subscribe((data:any) => {
            // data can be a set of coordinates, or an error (if an error occurred).
            // data.coords.latitude
            // data.coords.longitude
            alert(JSON.stringify(data))
            this.geo.timesamp = data.timestamp;
            this.geo.latitude = data.coords.latitude;
            this.geo.longitude = data.coords.longitude;

        });*/

    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad GeocityPage');
    }

    initializeItems() {
        this.items = this.api.towns;
    }

    sendRequest() {
        this.api.newrequest.geo = this.geo;
        this.api.sendRequest()
        this.navCtrl.setRoot(HomePage);
        setTimeout(function () {
            window.location.reload();
        }, 10000)
    }

    setcity(city) {
        this.api.newrequest.city = city;
    }

    getItems(ev: any) {
        // Reset items back to all of the items
        this.initializeItems();

        // set val to the value of the searchbar
        let val = ev.target.value;

        // if the value is an empty string don't filter the items
        if (val && val.trim() != '') {
            let x = this.items.filter((item: any) => {
                return (item.name.toLowerCase().indexOf(val.toLowerCase()) > -1);
            });
            this.items = x.slice(0, 9);
        } else {
            return this.items = null;
        }
    }

    goProfile() {
        this.navCtrl.push(ProfilePage)
    }

    rmcity() {
        this.api.newrequest.city = null;
    }

}



